.nav {
    display: flex;
    margin-bottom: 10px;
}

.nav :not(:last-child) {
    margin-right: 18px;
}

.btn {
    display: block;
    color: #212121;
    font-size: 24px;
    cursor: pointer;
}

.btn:hover {
    color:rgb(255, 0, 0);
}

.btn-current {
    display: block;
    color:rgb(255, 0, 0);
    font-size: 24px;
    cursor: pointer;
}